$bg-color: #242424;
$primary-color: #fff;
$title-color: rgba(148, 82, 168, 0.568);

body {
  background-color: $bg-color;
  color: $primary-color;

}

h1 {
  color: $title-color;
}


#searchinput {
  border-radius: 50px;
  height: 2em;
  width: 25%;
  font-size: 22px;
  background-color: #242424;
  border: 2px solid #fff;
  padding: 10px 20px;
  color: #fff;
  margin: 90px auto;
}

.firstcontainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.title {
  font-size: 70px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  margin-bottom: -20px;
}

p.text-white {
  font-size: 26px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

#pageContent {
  margin-left: 200px;
  margin-right: 200px;
}


